import { Directive, HostListener, ElementRef, Input } from '@angular/core';

/**
 * Generated class for the AutosizeDirective directive.
 *
 * See https://angular.io/api/core/Directive for more info on Angular
 * Directives.
 */
@Directive({
  selector: '[autosize]' // Attribute selector
})

export class AutosizeDirective {
  @Input()maxHeight;
 
  @HostListener('input', ['$event.target'])
  onInput(textArea:HTMLTextAreaElement):void {
    let textArea1 = textArea;
    this.adjust();
  }
  
  @HostListener('propertychange', ['$event.target'])
  onChange(textArea:HTMLTextAreaElement):void {
    let textArea1 = textArea;
    this.adjust();
  }

  @HostListener('ionBlur', ['$event.target'])
  onBlur(textArea:HTMLTextAreaElement):void {
    let textArea1 = textArea; 
    setTimeout(()=>{
      this.adjust();
    },100) 
  }
 
  constructor(public element:ElementRef) { 
  }
 
  ngOnInit():void {  
    setTimeout(() => this.adjust(0,true), 0);
  }

  adjust(textAreaHeight?:number,isInit?:boolean):void {
    const textArea = this.element.nativeElement.getElementsByTagName('textarea')[0];
    if(isInit){
      textArea.rows = 1
    }
    textArea.style.overflow = 'hidden';
    textArea.style.height = 'auto';
    if(textAreaHeight){
      textArea.style.height = textAreaHeight + 'px'; 
    }else if(this.maxHeight && this.maxHeight < textArea.scrollHeight){
      textArea.style.height = this.maxHeight + 'px'; 
      textArea.style.overflow = 'scroll';
    }else{
      textArea.style.height = textArea.scrollHeight + 'px';
    }
    
  }

 
}



