import { Component, ViewChild,  ElementRef } from '@angular/core';
import { IonicPage, NavController, ViewController, NavParams, Events, Content } from 'ionic-angular';
import { Session } from '../../../services/session';
import { Blog } from '../../../models/blog';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Utility } from './../../../services/utility';
import { Keyboard } from '@ionic-native/keyboard';
import { NativeService } from './../../../services/native-service';


@IonicPage()
@Component({
  selector: 'page-blog-add',
  templateUrl: 'blog-add.html',
})
export class BlogAddPage {
  currentUser: any;
  isLogin: Boolean = false;
  isEmployee: Boolean = false;
  inputText: string;
  form: FormGroup;
  topic: any;
  choosenImages: any = [];
  @ViewChild('footer') footer;//footer element
  @ViewChild(Content) content: Content;
  @ViewChild('fileInput') fileInput;
  location: string = "你在哪里";
  privacy: string = "公开";

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public session: Session,
    public formBuilder: FormBuilder,
    public events: Events,
    public keyboard: Keyboard,
    public nativeService: NativeService,
    public viewCtrl: ViewController ) {
    if (this.session.user != null) {
      this.currentUser = this.session.user;
      this.isLogin = true;
    }
    if (this.currentUser && this.currentUser.employeeInfo) {
      this.currentUser.pictureUrl = this.currentUser.pictureUrl || "assets/img/avatar-round.png";
    }
    this.form = formBuilder.group({
      inputcontent: [this.navParams.data.inputContent || '', Validators.required]
    });
    //this.topic=this.navParams.data||false;
    this.events.subscribe('at-someone:choosen', (data) => {
      if (!this.form.value.inputcontent) { this.form.value.inputcontent = ''; }
      this.inputText = this.form.value.inputcontent + "@" + data.realname;
    });

    this.events.subscribe('topic:choosen', (data) => {
      if (!this.form.value.inputcontent) { this.form.value.inputcontent = ''; }
      this.inputText = data.title + this.form.value.inputcontent;
    });

    this.keyboard.onKeyboardShow().subscribe((e: any) => {
      this.setFooterPosition(e.keyboardHeight)
      this.ContentResize()
    });
    this.events.subscribe("location:choosed", (data) => {
      this.location = data.name;
    })
    this.events.subscribe("privacy:done", (data) => {
      this.privacy = data;
    })

  }

  ionViewDidLoad() {

  }

  spliceImages(index){
    this.choosenImages.splice(index,1);
  }
  setFooterPosition(paddingBottom: number) {
    this.footer.nativeElement.style.paddingBottom = paddingBottom + 'px'
  }
  ContentResize() {
    setTimeout(() => {
      this.content.resize();
      setTimeout(() => {
        this.scrollToBottom();
      }, 50)
    }, 50)
  }
  scrollToBottom() {
    setTimeout(() => {
      this.content.scrollToBottom();
    }, 10);
  }


  publishBlog() {
    this.form.controls['inputcontent'];
    if (this.location = "你在哪里") { this.location = '' }
    let blog = {
      id: 1,
      author: {
        userId: this.currentUser.userId,
        userName: this.currentUser.employeeName,
        nickName: this.currentUser.nickName,
        hasPassword: this.currentUser.hasPassword,
        isEmployee: this.currentUser.isEmployee,
        pictureUrl: this.currentUser.pictureUrl,
        fansCount: 0
      },
      site: this.currentUser.employeeInfo.site,
      content: this.form.value.inputcontent,
      imgs: this.choosenImages,
      location: this.location,
      redirectUrl: 'string',
      publishTime: Utility.dateFormat(new Date(), 'yyyy-MM-dd HH:mm:ss'),
      readCount: 0,
      zanCount: 0,
      commentCount: 0,
      shareCount: 0,
    }
    this.events.publish('blog:add', blog);
    this.viewCtrl.dismiss();
  }
  openPhoneGallery() {
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByPhotoLibrary({//从相册多选
        destinationType: 0//期望返回的图片格式,0图片字符串
      }).subscribe(img => {
        if (!this.choosenImages) { this.choosenImages = []; }
        this.choosenImages.push({ pictureUrl: img });
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      this.fileInput.nativeElement.click();

    }
  }


  goback(){
    this.navCtrl.pop()
  }
  processWebImage(event) {
    let filesList = event.target.files;
    for(let file of filesList){ 
      let reader = new FileReader();
      reader.onload = (readerEvent) => { 
        let imageData = (readerEvent.target as any).result;
        if (!this.choosenImages) { this.choosenImages = []; }
        this.choosenImages.push({ pictureUrl: imageData });
      };
      reader.readAsDataURL(file);
    }  
  }

  openCamera() {
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByCamera({
        destinationType: 0//期望返回的图片格式,1图片路径,0 base64編碼
      }).subscribe(img => {
        if (!this.choosenImages) { this.choosenImages = []; }
        this.choosenImages.push({ pictureUrl: img });
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      if (!this.choosenImages) { this.choosenImages = []; }
      this.choosenImages.push({ pictureUrl: 'assets/img/puppy-2.jpg' });
    }

  }
  openFrindList() {
    //let time=Utility.dateFormat(new Date(),'yyyy-MM-dd HH:mm:ss');
    this.navCtrl.push('AtSomeonePage')
  }
  openTopicPage() {
    this.navCtrl.push('TopicChooserPage')
  }
  openQuestionPage() {
    this.navCtrl.push('QuestionAddPage')
  }
  setLocation() {
    this.navCtrl.push('LocationChooserPage');
  }
  setPrivacy() {
    this.navCtrl.push('PrivacySettingPage');
  }
 
}



